import { IconButton } from "@mui/material";
import { NavLink } from "react-router-dom";

const SidebarItem = ({ icon, to }) => (
  <IconButton
    size="large"
    edge="start"
    color="inherit"
    sx={{ margin: "auto"}}
  >
    <NavLink exact to={to} activeStyle={{ color: "#d00" }}>
      {icon && icon}
    </NavLink>
  </IconButton>
);

export default SidebarItem;
